.example {
  background-color: @color-basic-grey;
}

.example__wrapper {
  margin-left: 20px;
  margin-right: 20px;
}

@media (min-width: @tablet-width) {
  .example__wrapper {
    margin-left: 30px;
    margin-right: 30px;
  }
}

.example__heading {
  margin: 0;
  padding-top: 25px;
  padding-bottom: 25px;
  font-family: @font-heading;
  font-size: 36px;
  line-height: 40px;
}

@media (min-width: @tablet-width) {
  .example__heading {
    font-size: 60px;
    line-height: 60px;
  }
}

.example__text {
  margin: 0;
  padding-bottom: 20px;
  color: @color-dark-grey-1;
}

@media (min-width: @tablet-width) {
  .example__text {
    font-size: 16px;
    line-height: 24px;
  }
}

.example__title {
  font-size: 12px;
  line-height: 12px;
  background-color: @color-basic-grey;
}

.example__definition {
  font-family: @font-heading;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
}

.example__spending {
  font-family: @font-heading;
  line-height: 20px;
}
